.body{

  font-family: "Montserrat", sans-serif;
}

label{
  font-family: "Montserrat", sans-serif;
}

div{
  font-family: "Montserrat", sans-serif;
}

.container-sc{
  background-color: white;
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-rows: 1fr 8fr 1fr;
  gap: 5px;

}

.header-sc {
  background-color: #fca311;
  grid-row: 1;
  grid-column: 1 /-1 ;

}


.header-sc h1 {
  color: #fff;
  font-weight: 200;
  font-family: "Montserrat", sans-serif;
  margin: 20px;
}


.content-sc{
  grid-row:2 ;
  grid-column: 1;
}

.footer{
  background-color: #14213d;
  grid-row: 3;
  grid-column: 1 / -1;
}


 .card-income {
  border-radius: 4px;
  position: relative;
  padding: 25px 15px;
  background-color: white;
  margin: 40px 5px;
  height: 230px;
  width: 430px;
  box-shadow: 0 2px 5px #ccc;
  font-size: 14px;
  text-align: left;
}

.button-demography{
  min-width: 70px;
  height: 40px;
  padding: 16px 24px;
  border-radius: 4px;
  border: none;
  background: #141414;
  color: #fff;
  font-size:12px;
  text-align: center;

}

.button-income{
  min-width: 70px;
  height: 40px;
  padding: 16px 24px;
  border-radius: 4px;
  border: none;
  background: #141414;
