}

.container-sc .income-grid{
  display: grid;
  gap: 10px;

 }

.container-sc .grid-style-2 {
  display: grid;
  gap: 10px;
  grid-template-columns: 3fr 3fr 3fr; 
  
  }
  .container-sc .grid-box{
    display: grid;
    gap: 10px;
    grid-template-columns: 4fr 1fr; 
  }



  .card-2 {
