    .card {
      border-radius: 4px;
      position: relative;
      padding: 25px 15px;
      background-color: #f8f9fa;
      margin: 10px 20px;
      height: 90px;
      width: auto;
      box-shadow: 0 2px 5px #ccc;
      font-family: "Montserrat", sans-serif;
