      text-align: left;
    }

    .calculate{
      border-radius: 4px;
      position: relative;
      padding: 25px 15px;
      background-color: white;
      margin: 10px 0;
      height: 70vh;
      width: 400px;
      box-shadow: 0 2px 5px #ccc;
      font-family: "Montserrat", sans-serif;
      font-size: 14px;
      text-align: center;
      justify-content: center;
    }

    .img{
      width: 60px;
      height: 60px;
      justify-content: center;
    }

    .income-group{
      margin: 20px 0 0 0;
    }

    .total-expenditure{
      margin: 20px 0 0 0;
    }



    .tab-list {  border-bottom: 1px solid #ccc;
      padding-left: 0;
    }
    
    .tab-list-item {
      display: inline-block;
      list-style: none;
      margin-bottom: -1px;
      padding: 0.5rem 0.75rem;
    }
    
    .tab-list-active {
      background-color: white;
      border: solid #ccc;
      border-width: 1px 1px 0 1px;
    }

    .food-policy-container{
      background-color: white;
      padding: 60px;


    }

    .box{
      display: flex;
      align-items: center;
      justify-content: center;
  
 
    }
  
